import React, {useState} from 'react'
import './Login.css'
import { getAuth, updateProfile ,createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";
import { useDispatch } from 'react-redux';
import {login} from './features/userSlice';

function Login() {

  const [fullName, setFullname] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [profileUrl, setProfileUrl] = useState('')

const dispatch = useDispatch();

  const LoginToApp = (e) => { 
     e.preventDefault();
     
     if(!email) {
      return alert("Email is required")
     }
     if(!password) {
      return alert("Password is required")
     }
    
     const auth = getAuth();
     
     signInWithEmailAndPassword(auth, email, password)
       .then((userCredential) => {
         console.log("login")
         // Signed in 
         const user = userCredential.user;
         dispatch(login({
          email : user.email,
          uid : user.uid,
          displayName : user.displayName,
          photoURL : user.photoURL
        }))
       })
       .catch((error) => {
         const errorCode = error.code;
         const errorMessage = error.message;
       });

  }

  const register = (e) => {
     if(!fullName) {
      return alert('Full name is required')
     }
     if(!email) {
      return alert('Email is required')
     }
     if(!password) {
      return alert('Password is required')
     }

     const auth = getAuth();
      createUserWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
          // Signed in 
          const user = userCredential.user;
          console.log(user);
          // ...\\\\  
          // this update the profile
        
          updateProfile(auth.currentUser, {
            displayName: fullName,
             photoURL: profileUrl
          }).then(() => {
            // Profile updated!
            dispatch(login({
              email : user.email,
              uid : user.uid,
              displayName : fullName,
              photoURL : profileUrl
            }))
          }).catch((error) => {
              console.log(" Error from updating Name and photo url : ",error)
              alert(error.message)
          });

        })
        .catch((error) => {
          const errorCode = error.code;
          const errorMessage = error.message;
           alert(error.message)
        });
  }

  return (
    <div className='login'>
          <img src={ require('./img/login-img.PNG')} alt="login form image" />

          <form>
              <input type="text"value={fullName} onChange={(e) => setFullname(e.target.value)} placeholder="Full Name (required if registering)" />

              <input type="text" value={profileUrl} onChange={(e) =>setProfileUrl(e.target.value)}  placeholder="profile pic url (optional)" />

              <input type="email" placeholder="Email is required" value={email} onChange={(e) => setEmail(e.target.value)} />

              <input type="password" placeholder="Password is required" value={password} onChange={(e) => setPassword(e.target.value)} />

              <button type='submit' onClick={LoginToApp}>Sign In</button>

              <p>Not a member? <span className='login__register' onClick={register}>Register Now</span></p>
          </form>
      </div>
  )
}

export default Login