import React from 'react'
import './Widget.css'
import InfoIcon from '@material-ui/icons/Info';
import FiberManualRecord from '@material-ui/icons/FiberManualRecord';

function Widget() {
const newsArticle = (heading,subtitle) => {
  
   return (
      <div className='widgets__article'>
           <div className='widgets__left__article'>
              <FiberManualRecord />
           </div>
           <div className='widgets__right__article'>
                 <h4>{heading}</h4>
                 <p>{subtitle}</p>
           </div>
      </div>
      )

}

  return (
    <div className='widgets'>
        <div className='widgets__header'>
            <h2>linkedin news</h2>
            <InfoIcon />
        </div>

         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("Best framework is react", "Thanks for the info")}
         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("React is back", "Solid explanation")}
         {newsArticle("React is back", "Solid explanation")}
      </div>
  )
}

export default Widget