import React, {useState,useEffect} from 'react'
import './Feed.css'
import CreateIcon from '@material-ui/icons/Create';
import ImageIcon from '@material-ui/icons/Image';
import SubscriptionIcon from '@material-ui/icons/Subscriptions';
import EventIcon from '@material-ui/icons/Event';
import CalenderViewDayIcon from '@material-ui/icons/ViewAgenda';
import InputOptions from './InputOptions';
import Post from './Post';
import DbFire from './Firebase';
import {collection, addDoc,getDocs, doc, onSnapshot, orderBy, query, setDoc, Timestamp} from 'firebase/firestore'
import { useSelector } from 'react-redux';
import {selectUser} from './features/userSlice';


function Feed() {

  const [posts, setPost] = useState([]);
  const [input, setInput] = useState('');
  const user = useSelector(selectUser);

  useEffect(() => {
    try {
      const q = query(collection(DbFire.db, 'posts'), orderBy('created', 'desc'))
        onSnapshot(q, (snapshots) => {
            setPost(snapshots.docs.map(doc => ({
                id: doc.id,
                data: doc.data()
            })))
            // console.log('found posts => ', posts)
          })

      } catch (err) {
          alert(err)
      }
  }, [])

  const sendPost = (e) => {
        e.preventDefault(); 

    const addmyPost = (async () => {
         const docRef  = await addDoc(collection(DbFire.db, "posts"), {
          name: user.displayName,
          description : user.email,
          message : input,
          created : Timestamp.now()
        });
       setInput('')
    })

    addmyPost();
 
   }

  return (
    <div className="feed">
        <div className="feed__inputContainer">
            <div className="feed__input">
                <CreateIcon /> 
                <form>
                    <input type="text" value={input} onChange={e => setInput(e.target.value)} />
                    <button onClick={sendPost} type="submit">Send</button>
                </form>
              
            </div>
            <div className='feed__inputOptions'>
                <InputOptions Icon={ImageIcon} title='Photo' color="#70B5F9" />
                <InputOptions Icon={SubscriptionIcon} title='Video' color="#E7A33E" />
                <InputOptions Icon={EventIcon} title='Event' color="#70B5F9" />
                <InputOptions Icon={CalenderViewDayIcon} title='Write article' color="#70B5F9" />
            </div>
        </div>
         
         {posts &&  posts.map((post, i) => 
         <>
          <Post key={i} name={post.data.name} description={post.data.description} message={post.data.message}/>
         </>)}
      
      </div>
  )
}

export default Feed