import { Avatar } from '@material-ui/core'
import React from 'react'
import { useSelector } from 'react-redux'
import {selectUser} from './features/userSlice'
import './HeaderOption.css'

function HeaderOption({avator,Icon,title, onClick}) {

  const user = useSelector(selectUser);

  {
    return (
      <div onClick={onClick} className="headetOption">
           {Icon && <Icon className="`headetOption__icon"/>}
          {avator &&  <Avatar className="headetOption__icon" src={user?.photoURL} > {user?.email[0]}</Avatar>} 
           <h3 className="headetOption__title">{title}</h3>
      </div>
    )
  }
}

export default HeaderOption