import { Avatar } from '@material-ui/core'
import React from 'react'
import { useSelector } from 'react-redux';
import './Sidebar.css'
import {selectUser} from './features/userSlice';

function Sidebar() {
  
  const user = useSelector(selectUser);
  

  const recentItem = (topic) => {
  return (
     <div className="sidebar__recentItem">
        <span className="sidebar__hash">#</span>
        <p>{topic}</p>
      </div>
    )  
 };

  return (
    
    <div className='sidebar'>
      <div className='sidebar__top'>
        <img src={require('./img/background.jpg')} alt='background image' />
           <Avatar src={user.profileUrl}  className="sidebar_avator"> {user.email[0]} </Avatar>
           <h2>{user.displayName}</h2>
           <h4>{user.email}</h4>
      </div>
      <div className='sidebar__stats'>
           <div className="sidebar__stat">
               <p>Who viewed you</p>
               <p className='sidebar__stat_number'>2.5k</p>
           </div>
           <div className="sidebar__stat">
               <p>Views on post</p>
               <p className='sidebar__stat_number'>3,000</p>
           </div>
      </div>
      <div className='sidebar__bottom'>
          <p>Recent</p>
          {recentItem('Reactjs')}
          {recentItem('Programming')}
          {recentItem('Softwareengineering')}
          {recentItem('Design')}
          {recentItem('Developer')}
     </div>
    </div>
  )
}

export default Sidebar